/* Create the hidden pseudo-element */
/* include the shadow for the end state */
.cpLeft::after {
  content: '';
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  opacity: 0;
  border-radius: 5px;
  box-shadow: 0 5px 15px rgba(217, 68, 54, 0.1);
  transition: opacity 0.3s ease-in-out;
}
.cpLeft:hover {
  -webkit-transform: scale(1.10, 1.10);
  transform: scale(1.10, 1.10);
  cursor: pointer;
}

.cpLeft:hover::after {
    opacity: 1;
}

.cpRight::after {
  content: '';
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  opacity: 0;
  border-radius: 5px;
  box-shadow: 0 5px 15px rgba(217, 68, 54, 0.1);
  transition: opacity 0.3s ease-in-out;
}
.cpRight:hover {
  -webkit-transform: scale(1.10, 1.10);
  transform: scale(1.10, 1.10);
  cursor: pointer;
}

.cpRight:hover::after {
    opacity: 1;
}

